.container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 150px;
  margin-left: 10%;
  margin-right: 8%;
}
.txt {
  font-size: 15px;
  align-items: flex-end;
  margin-right: 10%;
  width: 60%;
  color: black;
  font-style: italic;
  font-weight: bold;
}
.img {
  margin-top: 7%;
  width: 100%;
  justify-content: right;
